:root {
  /* Base color */
  --white: hsl(0, 0%, 98%);
  --black: hsl(0, 0%, 10%);
  /* Website color palette */
  --primary: hsl(0, 2%, 90%);
  --primary_light: hsl(214, 18%, 35%);
  --secondary: hsl(214, 18%, 25%);
  --accent: hsl(318, 49%, 50%);
  /* Shadow */
  --shadow: transparent;
  /* Font styles */
  --ubuntu: "Ubuntu", sans-serif;
  --roboto: "Roboto Mono", monospace;
}

.hidden {
  opacity: 0;
  transform: translateX(-100%);
  filter: blur(2em);
  transition: 800ms opacity ease-in-out 200ms, 800ms transform ease-in-out 200ms;
}

.show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
}

.idle {
  animation-name: idle_anim;
  animation-duration: 3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

.textbox_anim {
  animation-name: show_box;
  animation-duration: 400ms;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.text_anim {
  animation-name: show_text;
  animation-duration: 800ms;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  animation-delay: 400ms;
}

.scroll_anim {
  animation-name: show_scroll;
  animation-duration: 300ms;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}

/* KEYFRAMES */
@keyframes show_box {
  to {
    transform: scaleY(100%);
  }
}
@keyframes show_text {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes show_scroll {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes idle_anim {
  5% {
    transform: translateY(0.25em);
  }
  10% {
    transform: translateY(0);
  }
  15% {
    transform: translateY(0.25em);
  }
  20% {
    transform: translateY(-0.05em);
  }
  30% {
    transform: translateY(0.05em);
  }
  40% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
}
@media (prefers-reduced-motion) {
  .hidden {
    transition: none;
  }
}
/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  min-height: 100%;
  font-size: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

#root,
#__next {
  isolation: isolate;
}

body {
  font-family: var(--roboto);
  height: 100svh;
  background-color: var(--primary);
  font-size: 1em;
}

.header {
  height: 100vh;
  width: 100%;
  display: grid;
  place-content: center;
  grid-template-rows: 50% 35% 15%;
}
.header__title {
  place-self: center;
  height: fit-content;
  width: 16ch;
  font-size: clamp(2.5rem, 1.5rem + 2vw, 5rem);
  letter-spacing: 0.2ch;
  color: var(--secondary);
  text-align: center;
  text-shadow: 0.1em 0.1em var(--shadow);
}
.header__textbox {
  place-self: start;
  width: auto;
  margin-inline: auto;
  padding-inline: 1em;
  padding-block: 2em;
  border-left: 0.5em solid var(--secondary);
  border-right: 0.5em solid var(--secondary);
  transform: scaleY(0);
  overflow-x: hidden;
}
.header__text {
  font-size: clamp(0.7rem, 0.5rem + 0.5vw, 1.25rem);
  color: var(--secondary);
  text-shadow: 0.1em 0.1em var(--shadow);
  transform: translateX(-100%);
  opacity: 0;
}
.header__scroll {
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  place-self: center;
  color: var(--secondary);
  padding: 1em;
  transform: translateY(100%);
  opacity: 0;
  transition: 250ms background-color ease-in-out, 250ms color ease-in-out;
}
.header__scroll > svg {
  width: 1.5em;
  height: 1em;
  padding-top: 0.25em;
  margin-inline: auto;
  fill: var(--secondary);
}
.header__scroll:hover {
  background-color: var(--secondary);
  color: var(--primary);
}
.header__scroll:is(:hover) > svg {
  fill: var(--primary);
}

main {
  width: 100%;
  background-color: var(--primary);
  display: flex;
  flex-direction: column;
}
main .card {
  width: 100%;
  height: 100svh;
  display: grid;
  place-content: center;
  grid-template-columns: 10% 50% 30% 10%;
  overflow: hidden;
  border-radius: 0.1em;
  background-color: var(--primary_light);
}
@media (min-width: 120em) {
  main .card {
    padding-inline: 10vw;
  }
}
main .card__images {
  position: relative;
  display: grid;
  grid-column: 2/3;
  height: min(25vw, 25em);
  grid-template-columns: 20% 80%;
  grid-template-rows: repeat(3, 1fr);
  column-gap: 1em;
  row-gap: 0.5em;
  overflow: hidden;
}
main .card__images > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  position: relative;
  cursor: pointer;
}
main .card__images > img.active {
  grid-row: 1/4;
  grid-column: 2/3;
  cursor: default;
}
main .card__text {
  color: var(--primary);
  padding: 2em;
  padding-top: 0;
  grid-column: 3/4;
}
main .card__text > h2 {
  font-weight: 600;
  text-transform: uppercase;
}
main .presentation {
  height: min(25vw, 25em);
  width: 100%;
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 30% 70%;
  column-gap: 0.5em;
}
main .presentation__image {
  width: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}
main .presentation__image--desktop {
  height: 100%;
}
main .presentation > .show-mobile {
  width: 100%;
  overflow-y: scroll;
}

footer {
  width: 100%;
  height: 144em;
  display: grid;
  align-items: center;
  background-color: var(--primary);
  color: var(--secondary);
  justify-content: space-between;
  padding-inline: 2.25em;
}

footer a {
  text-decoration: none;
  color: var(--secondary);
}

footer a:hover {
  color: var(--accent);
}

.projects-section-title {
  width: fit-content;
  padding-block: 1em;
  margin-inline: auto;
  font-size: clamp(1.5rem, 2rem + 2vw, 3rem);
  color: var(--secondary);
}

.projects-container {
  display: grid;
  width: 90%;
  position: relative;
  place-content: center;
  grid-template-columns: repeat(auto-fill, 25em);
  grid-gap: 5em;
  margin-inline: auto;
}

.project {
  width: 25em;
  height: 20em;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0.25em 0.25em 1em hsla(0, 0%, 50%, 0.5);
}
.project__image {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: 50% 90%;
  transform: scale(130%);
  filter: saturate(0.75);
}
.project__detail {
  position: absolute;
  width: fit-content;
  height: 2em;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: var(--secondary);
  color: var(--primary);
  padding: 0.25em;
  overflow: hidden;
  transition: 200ms height ease-in-out;
}
.project__detail:hover {
  height: fit-content;
}
.project__detail > div {
  width: fit-content;
  z-index: 999;
  display: grid;
  background-color: var(--secondary);
  grid-template-rows: repeat(4, 1fr);
}
.project__detail > div > p:not(:first-child) {
  padding-left: 1ch;
}
.project__demo {
  position: absolute;
  width: 100%;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  color: var(--primary);
  gap: 0.5em;
  padding: 0.5em;
}
.project__demo::before {
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  position: absolute;
  background-color: var(--secondary);
  z-index: 1;
  filter: opacity(0.8);
}
.project__demo button {
  width: fit-content;
  height: fit-content;
  position: relative;
  z-index: 999;
  padding: 0.5em;
  background-color: var(--primary);
  color: var(--secondary);
  border: none;
  transition: 200ms background-color ease-in-out, 200ms color ease-in-out;
  cursor: pointer;
}
.project__demo button:hover {
  background-color: var(--accent);
  color: var(--primary);
}
.project__popup {
  display: none;
  position: absolute;
}
.project__popup > img {
  width: 2.25rem;
  height: 2.25rem;
  cursor: pointer;
  box-sizing: content-box;
  padding: 1em;
  background-color: var(--primary);
  grid-row-start: 2;
}
.project__popup > img:hover {
  background-color: var(--accent);
}
.project__popup > span {
  font-weight: 300;
  color: var(--primary);
  grid-row-start: 3;
  justify-self: center;
  align-self: flex-start;
  cursor: pointer;
}

.nav {
  height: fit-content;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  z-index: 999;
  transition: 300ms margin ease-in-out, 300ms padding ease-in-out;
  justify-content: flex-end;
}
.nav--horizontal {
  background-color: var(--secondary);
  width: 100%;
  flex-direction: row;
  margin-top: 0;
  font-size: 1rem;
  padding-right: calc(1em + 5vw);
}
.nav--vertical {
  width: fit-content;
  margin-top: 2vh;
  padding-right: 0;
  font-size: 0.9rem;
  position: fixed;
  flex-direction: column;
  background-color: var(--primary);
  border-bottom: 0.15em solid var(--secondary);
  border-top: 0.15em solid var(--secondary);
  border-left: 0.15em solid var(--secondary);
  box-shadow: 0.25em 0.35em 0 var(--black);
}
.nav__item {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__item > a {
  width: 100%;
  position: relative;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  color: var(--primary);
  padding: 1.5em;
  transition: 300ms background-color ease-in-out, 300ms color ease-in-out;
}
.nav__item > .to-left {
  text-align: end !important;
  color: var(--secondary);
}
.nav__item > a:hover {
  background-color: var(--accent);
  color: var(--primary);
}